@import url(https://fonts.googleapis.com/css2?family=Ultra&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

.App {
  font-family: "Ultra", serif;
}

.App .header {
  display: block;
  position: fixed;
  width: 100%;
  height: 10vh;
  background-color: red;
}

.App .header #navbar {
  position: relative;
  height: 100%;
}

.App .header #navbar .nav-items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  float: right;
  width: 25%;
  height: 100%;
  list-style-type: none;
}

.App .header #navbar .nav-links {
  color: black;
  text-decoration: none;
  font-size: 28px;
}

.App #welcome-section {
  height: 100vh;
  background-color: gold;
}

.App #projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: blue;
  /* Add Zoom Animation */
  /* The Close Button (x) */
}

.App #projects #project-title {
  text-align: center;
  padding: 5vh 0;
  color: black;
  font-size: 4rem;
  font-weight: 400;
}

.App #projects #projects-tile {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  max-width: 1024px;
  width: 100%;
  height: 50%;
  margin: 2.5rem 0 7.5rem 0;
  background-color: whitesmoke;
  border: 1rem solid whitesmoke;
  outline: 1rem solid black;
}

.App #projects .categories {
  width: 328px;
  height: 328px;
  margin: 0 1.25px;
  padding-bottom: 350px;
  background-color: whitesmoke;
  border: 1rem solid whitesmoke;
  text-align: center;
}

.App #projects .categories .icon {
  cursor: pointer;
  border-radius: 50%;
}

.App #projects .categories:hover {
  background-color: #c8c8c8;
}

.App #projects #popup-window {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  z-index: 3;
}

.App #projects #popup-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75%;
  height: 75%;
  background-color: gold;
}

.App #projects .animate {
  -webkit-animation: animatezoom 0.6s;
  animation: animatezoom 0.6s;
}

@-webkit-keyframes animatezoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}

@keyframes animatezoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.App #projects .close {
  position: absolute;
  top: 0;
  right: 25px;
  color: #000;
  font-size: 35px;
  font-weight: bold;
}

.App #projects .close:hover,
.App #projects .close:focus {
  color: red;
  cursor: pointer;
}

.App .footer {
  display: block;
  height: 50vh;
  background-color: black;
}

@media only screen and (max-width: 768px) {
  .App .nav-items {
    display: none;
    background-color: gold;
  }
  .App #projects-tile {
    display: flex;
    flex-flow: column wrap;
    background-color: whitesmoke;
  }
  .App .categories {
    margin: 15px 0;
  }
}
